import { defineStore } from "pinia";
import { Ref, ref } from "vue";
import { PointEntity } from "@/kernel/entities/Point";
import { getPointList } from "@/kernel/api/methods/Point";
import { PointGetData } from "@/kernel/api/dto/Point";

export const useStorePoint = defineStore("point", () => {
  const points: Ref<PointEntity[]> = ref([]); // пункты на показ.
  const allPoints: Ref<PointEntity[]> = ref([]); // все пункты.
  const isStoreLoaded: Ref<boolean> = ref(false); // загружены ли все пунткы.
  const isStoreChanged: Ref<boolean> = ref(false); // состав стора поменялся, нужно для отрисовки карты.

  // приводит объект к нужному типу.
  function createPoint(data: PointGetData) {
    return new PointEntity({
      ...data,
    });
  }

  // наполняет стор allPoints, isStoreLoaded - true => стор наполненный.
  async function getAllPoints() {
    if (!isStoreLoaded.value) {
      const res = await getPointList({ size: 0 });
      if (!res.error && res.data.info) {
        allPoints.value = res.data.info.map((point) => createPoint(point));
        isStoreLoaded.value = true;
      } else {
        throw new Error(
          JSON.stringify(res.data?.info) || "Ошибка получения списка пунктов"
        );
      }
    }
  }

  // наполняет пункты из стора, без запросов к бекенду, можно вызывать только когда стор наполнен.
  // points - пункты которые нужно показать, можно вернуть их либо положить в стор points
  function getPoints(param: {
    point_id?: number;
    partner_id?: number;
    partner_name?: string;
    user_id?: number;
    noStore?: boolean; // если нужно получить данные но не трогать стор.
  }) {
    let pts: PointEntity[] = [];
    if (param.partner_id) {
      pts = allPoints.value.filter(function (point) {
        if (point.partner_id === param.partner_id) {
          return point;
        }
      });
    } else {
      // копирование данных, не ссылка.
      // Points = structuredClone(allPoints.value);
      pts = allPoints.value.map((a) => {
        return { ...a };
      });
    }

    if (param.user_id) {
      pts = pts.filter(function (point) {
        if (point.user_id === param.user_id) {
          return point;
        }
      });
    }

    if (param.partner_name) {
      const re = new RegExp(param.partner_name, "i");
      pts = pts.filter(function (point) {
        if (
          (point.partner_name && point.partner_name.match(re)) ||
          (point.partner_fullname && point.partner_fullname.match(re))
        ) {
          return point;
        }
      });
    }

    if (param.point_id) {
      pts = pts.filter(function (point) {
        if (point.point_id === param.point_id) {
          return point;
        }
      });
    }

    if (param.noStore) {
      // в стор points не кладем.
      return pts;
    } else {
      points.value = pts;
    }
  }

  // удаляем пункт из общего стора и стора на показ.
  function remove(param: { partner_id?: number; point_id?: number }) {
    if (param.point_id) {
      let index = allPoints.value.findIndex(
        (p) => p.point_id === param.point_id
      );
      if (index > -1) {
        allPoints.value.splice(index, 1);
        isStoreChanged.value = true;
      }

      index = points.value.findIndex((p) => p.point_id === param.point_id);
      if (index > -1) {
        points.value.splice(index, 1);
      }
    } else if (param.partner_id) {
      const temp1 = allPoints.value.filter(function (point) {
        if (point.partner_id !== param.partner_id) {
          return point;
        }
      });
      if (temp1.length != allPoints.value.length) {
        allPoints.value = temp1;
        isStoreChanged.value = true;
      }

      const temp2 = points.value.filter(function (point) {
        if (point.partner_id !== param.partner_id) {
          return point;
        }
      });
      if (temp2.length != points.value.length) {
        points.value = temp2;
      }
    }
  }

  // добавляем пункт в общий стор и стор на показ.
  function add(id: number, point: PointEntity) {
    let index = allPoints.value.findIndex((p) => p.point_id === id);
    if (index === -1) {
      allPoints.value.push(point);
      isStoreChanged.value = true;
    }

    index = points.value.findIndex((p) => p.point_id === id);
    if (index === -1) {
      points.value.push(point);
    }
  }

  // редактирование пункт в общий стор и стор на показ.
  function edit(point: PointEntity) {
    let index = allPoints.value.findIndex((p) => p.point_id === point.point_id);
    if (index > -1) {
      allPoints.value[index] = point;
      isStoreChanged.value = true;
    }

    index = points.value.findIndex((p) => p.point_id === point.point_id);
    if (index > -1) {
      points.value[index] = point;
    }
  }

  // изменение типа пункта в общем сторе и сторе на показ.
  function changeType(partner_id: number, type: string) {
    let j = 0;
    for (const i in allPoints.value) {
      if (allPoints.value[i].partner_id === partner_id) {
        allPoints.value[i].partner_type = type;
        j++;
      }
    }
    if (j) {
      isStoreChanged.value = true;
    }

    for (const i in points.value) {
      if (points.value[i].partner_id === partner_id) {
        points.value[i].partner_type = type;
      }
    }
  }

  // обнулить статус измения стора.
  function storeNotChanged() {
    isStoreChanged.value = false;
  }

  return {
    createPoint,
    getAllPoints,
    getPoints,
    remove,
    add,
    edit,
    changeType,
    storeNotChanged,
    points,
    allPoints,
    isStoreLoaded,
    isStoreChanged,
  };
});
