import { defineStore, storeToRefs } from "pinia";
import { computed, ComputedRef, Ref, ref } from "vue";
import {
  getPartnerGet,
  getPartnerList,
  getPartnerDelete,
  postPartnerAdd,
  postPartnerEdit,
} from "@/kernel/api/methods/Partner";
import { PartnerEntity, SearchByOptions } from "@/kernel/entities/Partner";
import { ValueTextType } from "@/kernel/entities/types";
import { PhoneForm } from "@/kernel/entities/types";
import { useStore } from "@/store";
import { useStorePoint } from "@/store/point";

export const useStorePartner = defineStore("partner", () => {
  const isShowDetails = ref(false); // открыта ли панель по деталям партнера.
  const selectPartnerId: Ref<number | null> = ref(null); // выбранный партнер айди.
  const partners: Ref<Record<string, PartnerEntity>> = ref({}); // список загруженных партнеров.
  const partnerTableOpt: Ref<SearchByOptions> = ref({
    page: 0,
    dir: "asc",
    order: "name",
  }); // параметры таблицы партнеров.

  const store = useStore();
  const { config, dictionary } = storeToRefs(store);
  const storePoint = useStorePoint();

  function createPartnerType(value: string) {
    const typeText = dictionary.value.partner.type[value];
    return new ValueTextType(value, typeText);
  }

  async function select(id: number | null) {
    if (id) {
      // если в сторе нет партнера сначало получаем данные по нему.
      if (partners.value[id] === undefined) {
        await fetch(id);
      }

      selectPartnerId.value = id;
      isShowDetails.value = true;
    } else {
      selectPartnerId.value = null;
      isShowDetails.value = false;
    }
  }

  // получить данные по одному партнеру.
  async function fetch(id: number, force?: boolean) {
    let isError = false;

    if (partners.value[id] === undefined || force) {
      const res = await getPartnerGet(id);

      if (res.error || !res.data.info) {
        isError = true;
      } else {
        partners.value[id] = new PartnerEntity({
          ...res.data.info,
          type: createPartnerType(res.data.info.type),
          phone: store.createPhoneForm(res.data.info.phone),
        });
      }
    }

    return isError;
  }

  // отдать данные по партнеру.
  async function get(id: number, force?: boolean) {
    if (partners.value[id] === undefined || force) {
      await fetch(id, force);
    }

    return partners.value[id];
  }

  async function list({ search, user_id, page, dir, order }: SearchByOptions) {
    const options: Record<string, string | number> = {};

    const pts: PartnerEntity[] = [];
    let count = 0;

    search && (options.name = search);
    user_id && (options.user_id = user_id);
    page && (options.page = page);
    dir && (options.dir = dir);
    order && (options.order = order);
    const size = config.value.pagination?.partner?.per_page || 10;
    size && (options.size = size);
    const res = await getPartnerList(options);
    if (!res.error && res.data.info) {
      count = res.data.count || 0;
      for (const el of res.data.info) {
        const data = new PartnerEntity({
          ...el,
          type: createPartnerType(el.type),
          phone: store.createPhoneForm(el.phone),
        });
        pts.push(data);
        if (partners.value[el.partner_id] === undefined) {
          partners.value[el.partner_id] = data;
        }
      }
    } else {
      throw new Error(
        JSON.stringify(res.data?.info) || "Ошибка получения списка контрагентов"
      );
    }

    return { data: pts, count: count };
  }

  async function create(payload: any): Promise<void> {
    const res = await postPartnerAdd({
      ...payload,
      phone: store.createPhoneParam(payload.phone),
    });
    if (!res.error) {
      const partner_id = res.data.info.partner_id;
      const partner = new PartnerEntity({
        ...payload,
        partner_id: partner_id,
        type: createPartnerType(payload.type),
      });
      partners.value[partner_id] = partner;
    } else {
      throw new Error(
        JSON.stringify(res.data?.info) || "Ошибка создания контрагента"
      );
    }
  }

  async function edit(payload: any, id: number): Promise<void> {
    const res = await postPartnerEdit({
      ...payload,
      partner_id: id,
      phone: store.createPhoneParam(payload.phone),
    });

    if (!res.error && res.data.updated) {
      const partner = new PartnerEntity({
        ...payload,
        partner_id: id,
        type: createPartnerType(payload.type),
      });
      // обновляем стор партнеров.
      partners.value[id] = partner;
      // обновляем тип у поинтов, без перезагрузки стора поинтов.
      if (payload.type !== partners.value[id].type.value) {
        storePoint.changeType(id, payload.type);
      }
    } else {
      throw new Error(
        JSON.stringify(res.data?.info) || "Ошибка изменения контрагента"
      );
    }
  }

  async function remove(id: number): Promise<boolean> {
    let isError = false;
    const res = await getPartnerDelete(id);
    if (!res.error && res.data.updated) {
      if (id == selectPartnerId.value) {
        select(null);
      }
      delete partners.value[id];
      storePoint.remove({ partner_id: id });
    } else {
      isError = true;
    }

    return isError;
  }

  return {
    selectPartnerId,
    partners,
    list,
    create,
    edit,
    fetch,
    get,
    remove,
    select,
    createPartnerType,
    isShowDetails,
    partnerTableOpt,
  };
});
