import { checkAdminRoute } from "@/router/checkAdminRoute";
import NotFound from "@/views/pages/NotFound.vue";

export const admin = {
  path: "/admin",
  name: "admin",
  beforeEnter: checkAdminRoute,
  meta: { authRequire: true },
  children: [
    {
      path: "panel",
      component: () =>
        import(
          /* webpackChunkName: "admin" */ "../../views/pages/Lk/IndexPage.vue"
        ),
      children: [
        {
          path: "users",
          name: "adminUsers",
          component: () =>
            import(
              /* webpackChunkName: "adminUsers" */ "../../views/pages/admin/AdminUsers.vue"
            ),
        },
        {
          path: "roles",
          name: "adminRoles",
          component: () =>
            import(
              /* webpackChunkName: "adminRoles" */ "../../views/pages/admin/AdminRoles.vue"
            ),
        },
      ],
    },
    {
      path: "users/create",
      name: "adminUserCreate",
      component: () =>
        import(
          /* webpackChunkName: "adminUserCreate" */ "../../views/pages/admin/AdminUserCreatePage.vue"
        ),
    },
    {
      path: "users/edit/:id",
      name: "adminUserEdit",
      component: () =>
        import(
          /* webpackChunkName: "adminUserEdit" */ "../../views/pages/admin/AdminUserEditPage.vue"
        ),
    },

    {
      path: "roles/create",
      name: "adminRoleCreate",
      component: () =>
        import(
          /* webpackChunkName: "adminRoleCreate" */ "../../views/pages/admin/AdminRoleCreatePage.vue"
        ),
    },
    {
      path: "roles/edit/:role",
      name: "adminRoleEdit",
      component: () =>
        import(
          /* webpackChunkName: "adminRoleEdit" */ "../../views/pages/admin/AdminRoleEditPage.vue"
        ),
    },
  ],
};
