import { createRouter, createWebHistory, RouteRecordRaw } from "vue-router";
import NotFound from "@/views/pages/NotFound.vue";
import { useStoreAuth } from "@/store/auth";
import { checkAuthRoute } from "@/router/checkAuthRoute";
import { useStoreModal } from "@/store/modal";
import { AvailableModalsEnum } from "@/store/modal/types";
import { useResetStore } from "@/use/useResetStore";
import { useStoreUser } from "@/store/user";
import { useStorePoint } from "@/store/point";
import { useStoreSetting } from "@/store/setting";
import { useStore } from "@/store";
import { storeToRefs } from "pinia";
import { admin } from "@/router/modules/admin";

const routes: Array<RouteRecordRaw> = [
  {
    path: "/",
    name: "LoginLayout",
    component: () => import("../layouts/AuthLayout.vue"),
    beforeEnter: async () => {
      const auth = useStoreAuth();
      const isAuth = await auth.isAuthenticated();
      if (isAuth) {
        await router.replace({ name: "map" });
      }
      return !isAuth;
    },
    children: [
      {
        path: "",
        name: "auth",
        component: () => import("../views/pages/AuthPage.vue"),
      },
    ],
  },
  {
    path: "/logout",
    name: "logout",
    beforeEnter: async () => {
      const auth = useStoreAuth();
      await auth.logout();
      await router.replace({ name: "auth" });
      useResetStore().all(["main"]);
    },
    component: () =>
      import(/* webpackChunkName: "logout" */ "../views/pages/AuthPage.vue"),
  },
  {
    path: "/lk",
    name: "lk",
    meta: { authRequire: true },
    component: () =>
      import(/* webpackChunkName: "Lk" */ "../views/pages/Lk/IndexPage.vue"),
    beforeEnter: checkAuthRoute,
    children: [
      {
        path: "map",
        name: "map",
        component: () =>
          import(/* webpackChunkName: "map" */ "../views/pages/Lk/MapPage.vue"),
      },
      {
        path: "offers",
        name: "offers",
        component: () =>
          import(
            /* webpackChunkName: "offers" */ "../views/modules/LacOffers/LacOffers.vue"
          ),
      },
      {
        path: "chains",
        name: "chain",
        component: () =>
          import(
            /* webpackChunkName: "offers" */ "../views/pages/Lk/ChainPage.vue"
          ),
      },
      {
        path: "requests",
        name: "requests",
        component: () =>
          import(
            /* webpackChunkName: "requests" */ "../views/pages/Lk/RequestsPage.vue"
          ),
      },
      {
        path: "events",
        name: "events",
        component: () =>
          import(
            /* webpackChunkName: "events" */ "../views/pages/Lk/EventsPage.vue"
          ),
      },
      {
        path: "profile",
        name: "profile",
        component: () =>
          import(
            /* webpackChunkName: "profile" */ "../views/pages/Lk/ProfilePage.vue"
          ),
      },
    ],
  },
  {
    path: "/partner",
    beforeEnter: checkAuthRoute,
    meta: { authRequire: true },
    children: [
      {
        path: "create",
        name: "partnerCreate",
        component: () =>
          import(
            /* webpackChunkName: "partnerCreate" */ "../views/pages/partner/PartnerCreatePage.vue"
          ),
      },
      {
        path: "edit/:id(\\d+)",
        name: "partnerEdit",
        component: () =>
          import(
            /* webpackChunkName: "partnerEdit" */ "../views/pages/partner/PartnerEditPage.vue"
          ),
      },
      {
        path: "agent/create",
        name: "agentCreate",
        component: () =>
          import(
            /* webpackChunkName: "partnerAgentCreate" */ "../views/pages/partner/PartnerAgentCreatePage.vue"
          ),
      },
      {
        path: "agent/edit/:id(\\d+)",
        name: "agentEdit",
        component: () =>
          import(
            /* webpackChunkName: "partnerAgentEdit" */ "../views/pages/partner/PartnerAgentEditPage.vue"
          ),
      },
      {
        path: "point/create",
        name: "pointCreate",
        component: () =>
          import(
            /* webpackChunkName: "partnerPointCreate" */ "../views/pages/partner/PartnerPointCreatePage.vue"
          ),
      },
      {
        path: "point/edit/:id(\\d+)",
        name: "pointEdit",
        component: () =>
          import(
            /* webpackChunkName: "partnerPointEdit" */ "../views/pages/partner/PartnerPointEditPage.vue"
          ),
      },
    ],
  },
  {
    path: "/offer",
    beforeEnter: checkAuthRoute,
    meta: { authRequire: true },
    children: [
      {
        path: "create",
        name: "offerCreate",
        component: () =>
          import(
            /* webpackChunkName: "offerCreate" */ "../views/pages/partner/OfferCreatePage.vue"
          ),
      },
      {
        path: "edit/:id(\\d+)",
        name: "offerEdit",
        component: () =>
          import(
            /* webpackChunkName: "offerEdit" */ "../views/pages/partner/OfferEditPage.vue"
          ),
      },
    ],
  },
  {
    path: "/request",
    beforeEnter: checkAuthRoute,
    meta: { authRequire: true },
    children: [
      {
        path: "create",
        name: "requestCreate",
        component: () =>
          import(
            /* webpackChunkName: "requestCreate" */ "../views/pages/partner/RequestCreatePage.vue"
          ),
      },
      {
        path: "edit/:id(\\d+)",
        name: "requestEdit",
        component: () =>
          import(
            /* webpackChunkName: "requestEdit" */ "../views/pages/partner/RequestEditPage.vue"
          ),
      },
    ],
  },
  {
    path: "/event",
    beforeEnter: checkAuthRoute,
    meta: { authRequire: true },
    children: [
      {
        path: "create",
        name: "eventCreate",
        component: () =>
          import(
            /* webpackChunkName: "eventCreate" */ "../views/pages/events/EventCreatePage.vue"
          ),
      },
      {
        path: "edit/:id(\\d+)",
        name: "eventEdit",
        component: () =>
          import(
            /* webpackChunkName: "eventEdit" */ "../views/pages/events/EventEditPage.vue"
          ),
      },
    ],
  },
  admin,
  {
    path: "/profile",
    name: "profile",
    beforeEnter: checkAuthRoute,
    meta: { authRequire: true },
    component: () =>
      import(
        /* webpackChunkName: "profile" */ "../views/pages/profile/ProfilePage.vue"
      ),
  },
  { path: "/:pathMatch(.*)*", name: "NotFound", component: NotFound },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

router.beforeEach(async (to, from, next) => {
  const modals = useStoreModal();
  const store = useStore();
  const storeUser = useStoreUser();
  const storePoint = useStorePoint();
  const storeSetting = useStoreSetting();
  const { me } = storeToRefs(storeUser);

  modals.open(AvailableModalsEnum.LOADER);
  try {
    await store.init();
    if (to.meta.authRequire && !me.value) {
      await storeUser.get();
    }
    if (me.value) {
      // по дефолту загружаем все пункты.
      await storePoint.getAllPoints();
      // загружаем системные настройки.
      await storeSetting.getSystem();
    }

    next();
  } catch (e) {
    modals.open(AvailableModalsEnum.ERROR, { text: (e as ErrorEvent).message });
  }
});
router.beforeResolve(() => {
  const modals = useStoreModal();
  modals.close();
});

export default router;
