import { defineStore, storeToRefs } from "pinia";
import { computed, reactive, Ref, ref } from "vue";
import {
  getUserGet,
  postUserEdit,
  postUserAdd,
  getUserList,
  getUserDelete,
} from "@/kernel/api/methods/User";
import { UserEntity } from "@/kernel/entities/User";
import { UserRole } from "@/kernel/entities/UserRole";
import { useStore } from "@/store";
import { UserEditData, UserGetData, UserAddData } from "@/kernel/api/dto/User";
import { ValueTextType } from "@/kernel/entities/types";
import { ApiRequestListOptionsType } from "@/kernel/api/types";
import { isEmptyObject } from "@/kernel/lib/isEmptyObject";

export const useStoreUser = defineStore("user", () => {
  const me: Ref<UserEntity | null> = ref(null); // текущий залогиненный юсер
  const selectUserId: Ref<number | null> = ref(null); // выбор айди юсера в селекте контргаентов.
  const { dictionary } = storeToRefs(useStore());
  const allUsers: Ref<Record<string, UserEntity>> = ref({}); // все юсеры.
  const roleOptions = computed((): ValueTextType[] => {
    return Object.entries(dictionary.value.user.role).map(([key, value]) => ({
      value: key,
      text: value,
    }));
  });

  function select(id: number | null) {
    selectUserId.value = id;
  }

  function createUser(data: UserGetData) {
    const roles = dictionary.value?.user?.role;
    const role = new UserRole(data.role, roles ? roles[data.role] : "Unknown");
    return new UserEntity({
      ...data,
      role,
    });
  }

  async function get() {
    const res = await getUserGet();
    if (!res.error && res.data?.info) {
      me.value = createUser(res.data.info);
    }
  }

  async function remove(id: number) {
    let isError = false;
    const res = await getUserDelete(id);
    if (!res.error && res.data.updated) {
      delete allUsers.value[id];
    } else {
      isError = true;
    }
    return isError;
  }

  async function add(payload: UserAddData) {
    const res = await postUserAdd(payload);

    if (res.error) {
      throw new Error(
        res.data.info
          ? JSON.stringify(res.data.info, null, 2)
          : "Не удалось создать пользователя"
      );
    } else {
      // добавим в общий стор.
      allUsers.value[res.data.info.user_id] = createUser({
        ...payload,
        user_id: res.data.info.user_id,
      } as UserGetData);
    }

    return res;
  }

  async function edit(payload: UserEditData) {
    const res = await postUserEdit(payload);

    if (!res.error && res.data.updated) {
      // обновляем свои данные.
      if (me.value?.user_id === payload.user_id) {
        me.value = createUser({ ...payload });
      }
      // обновить стор по всем юсерам, туду сделать me cсылкой на стор всех юсеров.
      allUsers.value[payload.user_id] = createUser({ ...payload });
    } else {
      throw new Error(
        res.data.info
          ? JSON.stringify(res.data.info, null, 2)
          : "Не удалось отредактировать пользователя"
      );
    }

    return res;
  }

  async function list(params?: ApiRequestListOptionsType) {
    const res = await getUserList(params);
    let users: UserEntity[] = [];
    let count = 0;

    if (!res.error && res.data.info) {
      users = res.data.info.map((user) => createUser(user));
      count = res.data.count || 0;
    } else {
      throw new Error(
        JSON.stringify(res.data?.info) ||
          "Ошибка получения списка пользователей"
      );
    }

    return { data: users, count: count };
  }

  async function getAllUsers(force?: boolean) {
    if (isEmptyObject(allUsers.value) || force) {
      console.log("get all users");
      const res = await getUserList({ size: 0 });
      if (!res.error && res.data.info) {
        for (const el of res.data.info) {
          allUsers.value[el.user_id] = createUser(el);
        }
      } else {
        throw new Error(
          JSON.stringify(res.data?.info) ||
            "Ошибка получения списка пользователей"
        );
      }
    }
  }

  return {
    me,
    selectUserId,
    select,
    get,
    edit,
    add,
    list,
    remove,
    roleOptions,
    createUser,
    allUsers,
    getAllUsers,
  };
});
