import { defineStore } from "pinia";
import { computed, onMounted, onUnmounted, reactive, Ref, ref } from "vue";
import { ReactiveVariable } from "vue/macros";
import {
  ApiSchemeDictionaryType,
  PaginationSchemeInit,
} from "@/kernel/api/types";
import { getTypeList } from "@/kernel/api/methods/getTypeList";
import { debounce } from "@/kernel/lib/debounce";
import { getSystemInit } from "@/kernel/api/methods/getSystemInit";
import { PhoneForm, PhoneParam } from "@/kernel/entities/types";

export type ConfigType = {
  api_timeout: number;
  update_interval: number;
  api_origin: string;
  version: number;
  google_map_api_key: string;
  pagination?: PaginationSchemeInit;
};

export const useStore = defineStore("main", () => {
  const isInit = ref(false);
  const config: ReactiveVariable<ConfigType> = reactive({
    api_origin: process.env.VUE_APP_API_ORIGIN,
    api_timeout: Number(process.env.VUE_APP_API_TIMEOUT),
    update_interval: Number(process.env.VUE_APP_DATA_UPDATE_INTERVAL),
    version: Number(process.env.VUE_APP_VERSION),
    google_map_api_key: process.env.VUE_APP_GOOGLE_MAP_API_KEY,
    pagination: {},
  });
  const dictionary: Ref<ApiSchemeDictionaryType> = ref(
    {} as ApiSchemeDictionaryType
  );
  const windowWidth: Ref<number> = ref(
    window.document.documentElement.clientWidth
  );
  const isShowAside = ref(true);

  function changeWindowWidth() {
    windowWidth.value = window.document.documentElement.clientWidth;
  }

  const onResize = debounce(changeWindowWidth, 500);

  onMounted(() => {
    window.addEventListener("resize", onResize);
  });
  onUnmounted(() => {
    window.removeEventListener("resize", onResize);
  });

  async function fetchDictionary() {
    const res = await getTypeList();
    if (res && !res.error && res.data.info) {
      dictionary.value = res.data.info;
    } else {
      throw new Error("Ошибка. Словарь не был загружен. Попробуйте позже");
    }
  }

  async function init() {
    if (!isInit.value) {
      const res = await getSystemInit();
      if (res.data.info && !res.error) {
        const data = res.data.info;
        if (config.pagination && data.pagination) {
          config.pagination.event = data.pagination.event;
          config.pagination.offer = data.pagination.offer;
          config.pagination.partner = data.pagination.partner;
          config.pagination.request = data.pagination.request;
          config.pagination.user = data.pagination.user;
        }
        if (data.google_map_api_key)
          config.google_map_api_key = data.google_map_api_key;
      }
      await fetchDictionary();
      isInit.value = true;
    }
  }

  const pointSubjects = computed(() => {
    return dictionary.value.point.subject;
  });

  function formatPhone(phone: string) {
    const match = phone.match(/^(\d)(\d{3})(\d{3})(\d\d)(\d\d)$/);
    if (match) {
      return `+${match[1]} (${match[2]}) ${match[3]}-${match[4]}-${match[5]}`;
    }
    return phone;
  }

  function createPhoneForm(value?: string) {
    const phoneForm: PhoneForm[] = [];
    if (value) {
      const phoneObj = JSON.parse(value);
      if (phoneObj && phoneObj.length) {
        for (const phone of phoneObj) {
          phoneForm.push({
            phone: formatPhone(phone.phone),
            extra: phone.extra,
            type: {
              value: phone.type,
              text: dictionary.value.phone.type[phone.type],
            },
          });
        }
      }
    }
    return phoneForm;
  }

  function createPhoneParam(value?: PhoneForm[]) {
    const phoneParam: PhoneParam[] = [];
    if (value && value.length) {
      for (const phone of value) {
        const newPhone: any = {
          phone: phone.phone.replace(/\D/g, ""),
          type: phone.type.value,
        };
        if (phone.extra) newPhone["extra"] = phone.extra;
        phoneParam.push(newPhone);
      }
    }
    return phoneParam;
  }

  return {
    config,
    dictionary,
    windowWidth,
    fetchDictionary,
    isShowAside,
    pointSubjects,
    isInit,
    init,
    formatPhone,
    createPhoneForm,
    createPhoneParam,
  };
});
