import {
  ApiBackendInterface,
  createApiBackend,
} from "@/kernel/lib/ApiBackend/ApiBackend";
import { useStore } from "@/store";
import { storeToRefs } from "pinia";

let http: ApiBackendInterface;

export function getHttp(recreate = false) {
  const store = useStore();

  const { config } = storeToRefs(store);
  if (!http || recreate) {
    http = createApiBackend({
      baseURL: config.value.api_origin,
      timeout: config.value.api_timeout,
    });
  }

  return http;
}
