import { getActivePinia, Pinia, Store } from "pinia";

interface ExtendedPinia extends Pinia {
  _s: Map<string, Store>;
}

export function useResetStore() {
  const pinia = getActivePinia() as ExtendedPinia;

  if (!pinia) {
    throw new Error("There is no active Pinia instance");
  }

  function resetAll(excluded: string[] = []) {
    pinia._s.forEach((store) => {
      if (excluded.some((id) => store.$id === id)) return;
      store.$reset();
    });
  }

  const resetStores: {
    [key: string]: () => void;
    all: (excluded?: string[]) => void;
  } = {
    all: resetAll,
  };

  pinia._s.forEach((store, name) => {
    resetStores[name] = () => store.$reset();
  });

  return resetStores;
}
