import { defineStore } from "pinia";
import { reactive, Ref, ref } from "vue";
import { AvailableModalsEnum, ModalParamsType } from "@/store/modal/types";

export const useStoreModal = defineStore("modal", () => {
  const activeModal: Ref<AvailableModalsEnum | null> = ref(null); // тип модалки (сексес, еррор, ...)
  const modalParams: ModalParamsType | null = ref(null);
  const isLocked = ref(false); // флаг блокировки, если true модалка не показывается.

  const open = (
    name: AvailableModalsEnum,
    params?: ModalParamsType,
    lock?: boolean // насильно выставить флаг блокировки
  ): void => {
    if (isLocked.value) return;

    isLocked.value = !!lock;
    activeModal.value = name;
    modalParams.value = reactive(params || {});
  };
  const close = (target?: string, unlock?: boolean): void => {
    if (unlock) {
      isLocked.value = false;
    }
    if (isLocked.value) return;

    if (target !== "bg") {
      activeModal.value = null;
      modalParams.value = null;
    }
  };

  return {
    activeModal,
    modalParams,
    isLocked,
    open,
    close,
  };
});
