import { LacSelectOption } from "@/views/components/form/types";

export interface BaseEntityInterface {
  getData(): any;
  getId(): number;
}

export class ValueTextType {
  public value!: string;
  public text!: string;
  public val?: number;

  constructor(value: string, text: string, val?: number) {
    this.value = value;
    this.text = text;

    typeof val === "number" && (this.val = val);
  }
}

export interface PhoneForm {
  phone: string;
  extra?: string;
  type: LacSelectOption;
  typeDisabled?: boolean;
}

export interface PhoneParam {
  phone: string;
  extra?: string;
  type: string;
}

export interface TableField {
  text: string;
  field: string;
  sort: string;
  noSort?: boolean;
}
