import { useStoreAuth } from "@/store/auth";
import { useStoreUser } from "@/store/user";
import { storeToRefs } from "pinia";

export const checkAdminRoute = async (to: any, from: any, next: any) => {
  const auth = useStoreAuth();
  const isAuth = await auth.isAuthenticated();
  if (!isAuth) {
    next({ name: "auth" });
  } else {
    const storeUser = useStoreUser();
    const { me } = storeToRefs(storeUser);
    if (!me.value) {
      await storeUser.get();
    }

    if (me.value && me.value.role?.value !== "admin") {
      next({ name: "NotFound" });
    } else {
      next();
    }
  }
};
