import { getHttp } from "@/kernel/api/http";
import { ApiResponseSystemInitType } from "@/kernel/api/types";

export const getSystemInit = async (): Promise<ApiResponseSystemInitType> => {
  const res = await getHttp().get<ApiResponseSystemInitType | false>(
    "/system/init"
  );

  if (!res) {
    throw new Error(
      "Ошибка. Системные справочники не были получены, перезагрузите страницу"
    );
  }

  return res;
};
