import { BaseEntityInterface, ValueTextType } from "@/kernel/entities/types";
import { UserRole } from "@/kernel/entities/UserRole";

export type UserDataInterface = {
  name: string;
  role: UserRole;
  phone: string;
  email?: string;
  dob?: string;
  comment?: string;
};

export class UserEntity {
  public user_id: number;
  public name: string;
  public phone: string;
  public role: UserRole;
  public comment?: string;
  public dob?: string;
  public email?: string;

  constructor(params: UserEntity) {
    this.user_id = params.user_id;
    this.name = params.name;
    this.phone = params.phone;
    this.role = params.role;
    params.comment && (this.comment = params.comment);
    params.dob && (this.dob = params.dob);
    params.email && (this.email = params.email);
  }
}
