import { getHttp } from "@/kernel/api/http";
import { ApiResponseTypeListType } from "@/kernel/api/types";

export const getTypeList = async (): Promise<ApiResponseTypeListType> => {
  const res = await getHttp().get<ApiResponseTypeListType | false>(
    "/type/list"
  );

  if (!res) {
    throw new Error("Ошибка запроса getTypeList");
  }

  return res;
};
