import axios, { Axios, AxiosRequestConfig, AxiosResponse } from "axios";
import Cookies from "js-cookie";
import { COOKIE_KEYS } from "@/kernel/constants/CookieKeys";
import { useStoreAuth } from "@/store/auth";
import router from "@/router";
import { useStoreModal } from "@/store/modal";
import { AvailableModalsEnum } from "@/store/modal/types";
import { ERROR_CODES } from "@/kernel/api/types";

enum CRITICAL_ERRORS {
  DB_ERROR = "Ошибка на сервере баз данных, попробуйте перезагрузить страницу через пару минут или обратитесь к администратору",
  NO_ACCESS = "Ошибка. У вас нет доступа к данному ресурсу",
}

function getMessage(info: any, fallback: string) {
  return typeof info === "string" ? info : fallback;
}

export type ApiBackendParamsType = {
  contentType?: string;
  headers?: Record<string, string>;
  errorHandler?: (...args: unknown[]) => void;
  searchParams?: ApiBackendSearchParamsType;
};
export type ApiBackendBodyType = Record<string, any>;
export type ApiBackendSearchParamsType = Record<string, string | number>;

export interface ApiBackendInterface {
  get: <T>(
    action: string | URL,
    params?: ApiBackendParamsType
  ) => Promise<T | false>;
  post: <T>(
    action: string | URL,
    body?: ApiBackendBodyType,
    params?: ApiBackendParamsType
  ) => Promise<T | false>;
}

export type ApiBackendConfigType = {
  baseURL: string;
  timeout?: number;
  baseRequestParams?: Record<string, string>;
  headers?: Record<string, string>;
};

class ApiBackend implements ApiBackendInterface {
  private readonly baseURL: string;
  private readonly baseRequestParams: Record<string, string>;
  private readonly headers: Record<string, string>;
  private httpClient: Axios;
  private errorReceiver: null | ((...args: unknown[]) => void);
  private localErrorHandler: null | ((...args: unknown[]) => void);
  private isPending: boolean;
  private pendingInterval: null | number;
  private pendings: Array<() => Promise<any>>;

  constructor(config: ApiBackendConfigType) {
    this.baseURL = config.baseURL;
    this.baseRequestParams = config.baseRequestParams || {};
    this.headers = config.headers || {};
    this.errorReceiver = null;
    this.isPending = false;
    this.pendingInterval = null;
    this.pendings = [];
    this.localErrorHandler = null;

    this.httpClient = axios.create({ ...config });

    this.init = this.init.bind(this);
    this.run = this.run.bind(this);
    this.init();
  }

  private init() {
    // eslint-disable-next-line @typescript-eslint/no-empty-function
    this.errorReceiver = () => {};

    /** Добавляем экземпляру созданные обработчики для аутентификации и логов */
    this.httpClient.interceptors.request.use(<any>authInterceptor);
    this.httpClient.interceptors.response.use(apiErrorInterceptor);

    this.httpClient.interceptors.response.use(
      (response) => response,

      async (error) => {
        const { response = {}, request = {}, message = "" } = error;

        if (
          message === CRITICAL_ERRORS.DB_ERROR ||
          message === CRITICAL_ERRORS.NO_ACCESS
        ) {
          throw new Error(message);
        }

        const currentUrl = request.responseURL
          ? new URL(request.responseURL)
          : new URL(location.href);

        if (response.status === 401) {
          if (currentUrl && currentUrl.href.indexOf("auth/delete") !== -1) {
            return error;
          }
          const originalConfig = error.config;
          if (this.isPending) {
            return new Promise((resolve) => {
              const interval = setInterval(() => {
                if (!this.isPending) {
                  clearInterval(interval);
                  resolve(axios(originalConfig));
                }
              }, 500);
            });
          } else {
            this.isPending = true;
            const res = await authResponseErrorInterceptor(error, this);
            this.isPending = false;
            setTimeout(this.run, 5);

            if (res) {
              return axios(originalConfig);
            }
          }
        }

        // TODO Реализовать при необходимости
        /*if (this.localErrorHandler) {
          this.localErrorHandler(error);
          this.localErrorHandler = null;
        } else {
          const err = {
            error: "request_error",
            error_text:
              "Что то пошло не так, попробуйте перезагрузить страницу или зайти попозже",
            event: error,
          };
          if (this.errorReceiver) {
            this.errorReceiver.receive(err);
          }
        }*/
        /** Как-то обрабатываем результат, завершенный ошибкой */
        return false;
      }
    );
  }

  private run() {
    if (this.pendings.length && !this.pendingInterval) {
      this.pendingInterval = setInterval(() => {
        const callback = this.pendings.pop();

        if (callback) {
          callback();
          if (!this.pendings.length && this.pendingInterval) {
            clearInterval(this.pendingInterval);
            this.pendingInterval = null;
          }
        }
      }, 200);
    }
  }

  async get<T>(action: string | URL, params: ApiBackendParamsType = {}) {
    const baseURL = this.baseURL;
    const url = new URL(baseURL + action);
    const requestHeaders: Record<string, string> = {};

    for (const key in Object.keys(this.baseRequestParams)) {
      url.searchParams.append(key, this.baseRequestParams[key]);
    }

    if (this.headers) {
      Object.assign(requestHeaders, {
        ...this.headers,
        ...(params.headers || {}),
      });
    }

    const { errorHandler = null, searchParams = {} } = params;
    this.localErrorHandler = errorHandler;

    Object.keys(searchParams).forEach((key: string) =>
      url.searchParams.append(key, String(searchParams[key]))
    );

    /*try {*/
    const res = await this.httpClient.request({
      method: "GET",
      url: String(url),
      headers: requestHeaders,
    });

    return res.data as T;
    /*} catch (e) {
      if (errorHandler && typeof errorHandler === "function") {
        errorHandler(e);
      } else {
        console.warn(e);
        // Если есть глобальная модалка в window то можно ее тут использовать
      }

      return false;
    }*/
  }
  async post<T>(
    action: string | URL,
    body: ApiBackendBodyType = {},
    params: ApiBackendParamsType = {}
  ) {
    const {
      contentType = "application/x-www-form-urlencoded",
      headers = {},
      errorHandler = null,
      searchParams = {},
    } = params;
    delete params["errorHandler"];
    this.localErrorHandler = errorHandler;
    let bodyResult: string | FormData;

    const baseURL = this.baseURL;
    const url = new URL(baseURL + action);

    Object.entries(searchParams).forEach(([key, value]) => {
      url.searchParams.append(key, value as string);
    });

    if (contentType.indexOf("application/json") !== -1) {
      Object.keys(this.baseRequestParams).forEach((key) => {
        if (!body[key]) {
          body[key] = this.baseRequestParams[key];
        }
      });
      bodyResult = JSON.stringify(body);
    }

    if (contentType === "application/x-www-form-urlencoded") {
      bodyResult = new FormData();

      Object.keys(this.baseRequestParams).forEach((key) => {
        if (bodyResult instanceof FormData) {
          bodyResult.append(key, this.baseRequestParams[key]);
        }
      });
      Object.keys(body).forEach((key) => {
        if (bodyResult instanceof FormData) {
          bodyResult.append(key, body[key]);
        }
      });
    }

    const requestHeaders = {
      "Content-Type": contentType,
    };

    if (this.headers) {
      Object.assign(requestHeaders, { ...this.headers, ...headers });
    }

    const config = {
      method: "POST",
      url: String(url),
      headers: requestHeaders,
      data: body,
    };

    /*try {*/
    const res = await this.httpClient.request(config);

    return res.data as T;
    /*} catch (e) {
      debugger;
      if (
        this.localErrorHandler &&
        typeof this.localErrorHandler === "function"
      ) {
        this.localErrorHandler(e);
        this.localErrorHandler = null;
      } else {
        // console.warn(e);
        // Если есть глобальная модалка в window то можно ее тут использовать
      }
      return false;
    }*/
  }
}

const authInterceptor = (config: AxiosRequestConfig) => {
  // достаём токент аутентификации пользователя, с LocalStorage, или cookies, например
  const authToken = Cookies.get(COOKIE_KEYS.ACCESS_TOKEN);
  if (authToken) {
    if (!config.headers) {
      config.headers = {} as Record<string, string>;
    }
    config.headers.Authorization = authToken;
  }

  return config;
};

const authResponseErrorInterceptor = async (
  error: any,
  context: ApiBackend
): Promise<boolean> => {
  const auth = useStoreAuth();
  await auth.logout();
  return new Promise((resolve) => {
    const modalStore = useStoreModal();
    const callback = () => {
      modalStore.close();
      router.replace("/");
      resolve(false);
    };
    modalStore.open(AvailableModalsEnum.ERROR, {
      text: "Ошибка авторизации, выполните вход заново",
      callback,
    });

    setTimeout(callback, 5000);
  });
  // TODO сделать запрос на сброс токена и в случае успеха вернуть true иначе false
};

const apiErrorInterceptor = (config: AxiosResponse) => {
  const data = config.data;

  if (data.error) {
    if (data.data?.code === ERROR_CODES.DB_ERROR) {
      //@ts-ignore
      config.message = getMessage(data.data.info, CRITICAL_ERRORS.DB_ERROR);
    } else if (data.data?.code === ERROR_CODES.NO_ACCESS) {
      //@ts-ignore
      config.message = getMessage(data.data.info, CRITICAL_ERRORS.NO_ACCESS);
    } else {
      return config;
    }
    return Promise.reject(config);
  }
  return config;
};

export function createApiBackend(
  config: ApiBackendConfigType
): ApiBackendInterface {
  const { baseURL, timeout } = config;

  return new ApiBackend({
    baseURL,
    timeout,
  });
}
