import { PhoneForm } from "@/kernel/entities/types";
import { ValueTextType } from "@/kernel/entities/types";

export class PartnerType extends ValueTextType {}

export interface PartnerInterface {
  inn: string;
  fullname: string;
  name: string;
  type: PartnerType;
  website?: string;
  email?: string;
  phone?: PhoneForm[];
  comment?: string;
}

export class PartnerEntity implements PartnerInterface {
  public partner_id!: number;

  public inn!: string;
  public fullname!: string;
  public name!: string;
  public type!: PartnerType;
  public website?: string;
  public email?: string;
  public phone?: PhoneForm[];
  public comment?: string;

  constructor(params: PartnerEntity) {
    this.partner_id = params.partner_id;
    this.inn = params.inn;
    this.fullname = params.fullname;
    this.name = params.name;
    this.type = params.type;
    params.website && (this.website = params.website);
    params.email && (this.email = params.email);
    params.phone?.length && (this.phone = params.phone);
    params.comment && (this.comment = params.comment);
  }
}

export interface SearchByOptions {
  search?: string;
  user_id?: number;
  page?: number;
  order?: string;
  dir?: string;
}
