import { getHttp } from "@/kernel/api/http";
import { ApiResponseType, ApiRequestListOptionsType } from "@/kernel/api/types";
import { NotRequestException } from "@/kernel/lib/exceptions/not-request.exception";
import {
  AgentGetData,
  AgentAddData,
  AgentEditData,
} from "@/kernel/api/dto/Agent";

export const getAgentDelete = async (
  agentId: number
): Promise<ApiResponseType> => {
  const res = await getHttp().get<ApiResponseType | false>("/agent/delete", {
    searchParams: {
      agent_id: agentId,
    },
  });

  if (!res) {
    throw new NotRequestException(getAgentDelete.name);
  }

  return res;
};

export const getAgentGet = async (
  agentId: number
): Promise<ApiResponseType<AgentGetData>> => {
  const res = await getHttp().get<ApiResponseType<AgentGetData> | false>(
    "/agent/get",
    {
      searchParams: {
        agent_id: agentId,
      },
    }
  );

  if (!res) {
    throw new NotRequestException(getAgentGet.name);
  }

  return res;
};

export const getAgentList = async (
  params?: ApiRequestListOptionsType & { partner_id?: number }
): Promise<ApiResponseType<AgentGetData[]>> => {
  const res = await getHttp().get<ApiResponseType<AgentGetData[]> | false>(
    "/agent/list",
    { searchParams: { ...params, size: 0 } }
  );

  if (!res) {
    throw new NotRequestException(getAgentList.name);
  }

  return res;
};

export const postAgentAdd = async (
  payload: AgentAddData
): Promise<ApiResponseType> => {
  const res = await getHttp().post<ApiResponseType | false>("/agent/add", {
    ...payload,
    phone: JSON.stringify(payload.phone),
  });

  if (!res) {
    throw new NotRequestException(postAgentAdd.name);
  }

  return res;
};

export const postAgentEdit = async (
  payload: AgentEditData
): Promise<ApiResponseType<AgentGetData[]>> => {
  const res = await getHttp().post<ApiResponseType<AgentGetData[]> | false>(
    "/agent/edit",
    {
      ...payload,
      phone: JSON.stringify(payload.phone),
    }
  );

  if (!res) {
    throw new NotRequestException(postAgentEdit.name);
  }

  return res;
};
