import { getHttp } from "@/kernel/api/http";
import { ApiResponseType, ApiRequestListOptionsType } from "@/kernel/api/types";
import { UserGetData, UserAddData, UserEditData } from "@/kernel/api/dto/User";
import { NotRequestException } from "@/kernel/lib/exceptions/not-request.exception";
import { ApiBackendSearchParamsType } from "@/kernel/lib/ApiBackend/ApiBackend";

export const getUserGet = async (
  user_id?: number | undefined
): Promise<ApiResponseType<UserGetData>> => {
  const searchParams: ApiBackendSearchParamsType = {};

  user_id && (searchParams.user_id = user_id);
  const res = await getHttp().get<ApiResponseType<UserGetData> | false>(
    "/user/get",
    { searchParams }
  );

  if (!res) {
    throw new NotRequestException(getUserGet.name);
  }

  return res;
};

export const getUserList = async (
  params?: ApiRequestListOptionsType
): Promise<ApiResponseType<UserGetData[]>> => {
  const res = await getHttp().get<ApiResponseType<UserGetData[]> | false>(
    "/user/list",
    { searchParams: { ...params } }
  );

  if (!res) {
    throw new NotRequestException(getUserList.name);
  }

  return res;
};

export const getUserDelete = async (
  userId: number
): Promise<ApiResponseType> => {
  const res = await getHttp().get<ApiResponseType | false>("/user/delete", {
    searchParams: {
      user_id: userId,
    },
  });

  if (!res) {
    throw new NotRequestException(getUserDelete.name);
  }

  return res;
};

export const postUserAdd = async (
  payload: UserAddData
): Promise<ApiResponseType> => {
  const res = await getHttp().post<ApiResponseType | false>(
    "/user/add",
    payload
  );

  if (!res) {
    throw new NotRequestException(postUserAdd.name);
  }

  return res;
};

export const postUserEdit = async (
  payload: UserEditData
): Promise<ApiResponseType> => {
  const res = await getHttp().post<ApiResponseType | false>(
    "/user/edit",
    payload
  );

  if (!res) {
    throw new NotRequestException(postUserEdit.name);
  }

  return res;
};
