import { defineStore } from "pinia";
import { Ref, ref } from "vue";
import { AgentEntity } from "@/kernel/entities/Agent";
import {
  getAgentList,
  getAgentDelete,
  postAgentAdd,
  postAgentEdit,
  getAgentGet,
} from "@/kernel/api/methods/Agent";
import { AgentGetData } from "@/kernel/api/dto/Agent";
import { useStore } from "@/store";

export const useStoreAgent = defineStore("agent", () => {
  const agents: Ref<Record<string, AgentEntity[]>> = ref({}); // список агентов.
  const store = useStore();

  // приводит объект к нужному типу.
  function createAgent(data: AgentGetData) {
    return new AgentEntity({
      ...data,
      phone: store.createPhoneForm(data.phone),
    });
  }

  // наполняет стор agents, если есть форс параметр то наполнит даже если данные в нем есть.
  async function getAgents(param: { partner_id: number; force?: boolean }) {
    if (param.partner_id) {
      if (agents.value[param.partner_id] === undefined || param.force) {
        const res = await getAgentList({
          partner_id: param.partner_id,
          size: 0,
        });
        if (!res.error && res.data.info) {
          agents.value[param.partner_id] = res.data.info.map((agent) =>
            createAgent(agent)
          );
        } else {
          throw new Error(
            JSON.stringify(res.data?.info) || "Ошибка получения списка агентов"
          );
        }
      }
    }
  }

  async function remove(param: { agentId: number; partnerId: number }) {
    const res = await getAgentDelete(param.agentId);
    if (!res.error && res.data.updated) {
      await getAgents({
        partner_id: param.partnerId,
        force: true,
      });
    } else {
      throw new Error("Ошибка удаления агента");
    }
  }

  async function add(payload: any) {
    const res = await postAgentAdd({
      ...payload,
      phone: store.createPhoneParam(payload.phone),
    });

    if (!res.error) {
      await getAgents({
        partner_id: payload.partner_id,
        force: true,
      });
    } else {
      throw new Error("Ошибка создания агента");
    }
  }

  async function edit(payload: any) {
    const res = await postAgentEdit({
      ...payload,
      phone: store.createPhoneParam(payload.phone),
    });

    if (!res.error && res.data.updated) {
      await getAgents({
        partner_id: payload.partner_id,
        force: true,
      });
    } else {
      throw new Error("Ошибка изменения агента");
    }
  }

  async function get(id: number) {
    const res = await getAgentGet(id);
    if (!res.error && res.data.info) {
      return createAgent(res.data.info);
    }
    throw new Error("Ошибка получения данных агента");
  }

  return {
    createAgent,
    getAgents,
    remove,
    agents,
    add,
    edit,
    get,
  };
});
