import { defineStore, storeToRefs } from "pinia";
import { Ref, ref, unref } from "vue";
import Cookies from "js-cookie";
import { COOKIE_KEYS } from "@/kernel/constants/CookieKeys";
import { getAuth, getAuthDelete } from "@/kernel/api/methods/Auth";
import { ERROR_CODES } from "@/kernel/api/types";
import { useStoreUser } from "@/store/user";
import { useStorePartner } from "@/store/partner";
import { useStoreAgent } from "@/store/agent";
import { useStorePoint } from "@/store/point";
import { useStore } from "@/store";

export const useStoreAuth = defineStore("auth", () => {
  const token = ref(Cookies.get(COOKIE_KEYS.ACCESS_TOKEN));
  const storePartner = useStorePartner();
  const storeAgent = useStoreAgent();
  const storePoint = useStorePoint();
  const storeUser = useStoreUser();
  async function isAuthenticated(): Promise<boolean> {
    return !!token.value;
  }

  async function login(password: string | Ref<string>): Promise<void> {
    const res = await getAuth(unref(password));

    if (res.error) {
      throw new Error(
        res.data.code === ERROR_CODES.AUTH_ERROR
          ? "Ошибка авторизации"
          : "Произошла какая то ошибка, попробуйте позже"
      );
    } else if (res.data.info) {
      token.value = res.data.info.token;
      Cookies.set(COOKIE_KEYS.ACCESS_TOKEN, res.data.info.token, {
        sameSite: "strict",
      });
    }
  }

  async function logout(): Promise<void> {
    try {
      await getAuthDelete();
    } catch (e) {
      console.warn(
        (e as ErrorEvent).message || "Ошибка разлогина, очищаем данные"
      );
    } finally {
      token.value = "";
      storeUser.$reset();
      storePoint.$reset();
      storePartner.$reset();
      storeAgent.$reset();
      Cookies.remove(COOKIE_KEYS.ACCESS_TOKEN, { sameSite: "strict" });
    }
  }

  return { token, login, logout, isAuthenticated };
});
