import { PhoneForm } from "@/kernel/entities/types";

export interface AgentDataInterface {
  partner_id: number;
  name: string;
  phone: PhoneForm[];
  role: string;
  dob?: string;
  email?: string;
  comment?: string;
}

export class AgentEntity {
  public agent_id!: number;
  public partner_id!: number;

  public name: string;
  public phone: PhoneForm[];
  public role: string;
  public dob?: string;
  public email?: string;
  public comment?: string;

  constructor(params: AgentDataInterface & { agent_id: number }) {
    this.agent_id = params.agent_id;
    this.partner_id = params.partner_id;
    this.name = params.name;
    this.phone = params.phone;
    this.role = params.role;
    params.dob && (this.dob = params.dob);
    params.email && (this.email = params.email);
    params.comment && (this.comment = params.comment);
  }
}
