import { PartnerGetData } from "@/kernel/api/dto/Partner";
export enum DIR_TYPES {
  ACS = "asc",
  DESC = "desc",
}

export enum ERROR_CODES {
  OK = "ok",
  BAD_PARAM = "bad param",
  NO_ACCESS = "no access",
  DB_ERROR = "db error",
  AUTH_ERROR = "auth error",
}

type ApiDataType<T = any> = {
  info?: T;
};

export type ApiErrorInfo = Record<string, string> | string[] | string;

type ApiResponseErrorType = {
  error: 1;
  data: ApiDataType<ApiErrorInfo> & {
    code:
      | ERROR_CODES.BAD_PARAM
      | ERROR_CODES.NO_ACCESS
      | ERROR_CODES.DB_ERROR
      | ERROR_CODES.AUTH_ERROR;
  };
};

type ApiResponseSuccessType<T> = {
  error: 0;
  data: ApiDataType<T | null> & {
    code: ERROR_CODES.OK;
    count?: number;
    password?: string;
    updated?: Record<string, any>;
  };
};

export type ApiResponseType<T = any> =
  | ApiResponseErrorType
  | ApiResponseSuccessType<T>;

// REQUESTS
export type ApiRequestListOptionsType = {
  page?: number;
  size?: number;
  dir?: DIR_TYPES;
  order?: string;
};

export type ApiRequestPartnerListOptionsType = ApiRequestListOptionsType & {
  name?: string;
};
export type ApiRequestPartnerAddPayloadType = {
  inn: string;
  fullname: string;
  name: string;
  type: string;
  website?: string;
  email?: string;
  comment?: string;
};

// RESPONCES
export type ApiResponseSystemInitType = ApiResponseType<ApiSchemeSystemInit>;
export type ApiResponsePartnerListType = ApiResponseType<PartnerGetData[]>;
export type ApiResponsePartnerGetType = ApiResponseType<PartnerGetData>;
export type ApiResponseTypeListType = ApiResponseType<ApiSchemeDictionaryType>;

// SHEMES
export type ApiSchemeAuth = {
  token: string;
};

export interface PaginationSchemeInit {
  event?: {
    per_page: number;
  };
  offer?: {
    per_page: number;
  };
  partner?: {
    per_page: number;
  };
  request?: {
    per_page: number;
  };
  user?: {
    per_page: number;
  };
}

export type ApiSchemeSystemInit = {
  api_timeout?: number;
  update_interval?: number;
  version?: number;
  pagination?: PaginationSchemeInit;
  google_map_api_key?: string;
};

export type ApiSchemeDictionaryType = {
  event: {
    type: Record<string, string>;
  };
  offer: {
    available_formula_by_subject: Record<string, string[]>;
    formula: Record<string, string>;
    property: Record<string, PropertyItem>;
    status: Record<string, string>;
    vat_rate: number;
  };
  partner: {
    type: Record<string, string>;
  };
  point: {
    class: Record<string, string>;
    subject: Record<string, { desc: string; val: number }>;
    type: Record<string, string>;
  };
  user: {
    role: Record<string, string>;
  };
  request: {
    payoff_base: Record<string, { desc: string; vat: number }>;
    status: Record<string, string>;
  };
  phone: {
    type: Record<string, string>;
  };
};

export type PropertyItem = {
  cond: Record<string, ConditionFormula>;
  desc: string;
};

type ConditionFormula = Record<string, ProductParams>;

type ProductParams = {
  after_dot?: number;
  fix?: number;
  type: string;
  max?: number;
  min?: number;
  step?: number;
  def?: number;
  link?: string;
};
