import { getHttp } from "@/kernel/api/http";
import { ApiRequestListOptionsType, ApiResponseType } from "@/kernel/api/types";
import { NotRequestException } from "@/kernel/lib/exceptions/not-request.exception";
import {
  PointEditData,
  PointAddData,
  PointGetData,
} from "@/kernel/api/dto/Point";

export const postPointEdit = async (
  payload: PointEditData
): Promise<ApiResponseType> => {
  const res = await getHttp().post<ApiResponseType | false>(
    "/point/edit",
    payload
  );

  if (!res) {
    throw new Error("Ошибка запроса редактирования пункта");
  }

  return res;
};

export const getPointGet = async (
  pointId: number
): Promise<ApiResponseType<PointGetData>> => {
  const res = await getHttp().get<ApiResponseType<PointGetData> | false>(
    "/point/get",
    {
      searchParams: {
        point_id: pointId,
      },
    }
  );

  if (!res) {
    throw new Error("Ошибка запроса получения пункта");
  }

  return res;
};

export const postPointAdd = async (
  payload: PointAddData
): Promise<ApiResponseType> => {
  const res = await getHttp().post<ApiResponseType | false>(
    "/point/add",
    payload
  );

  if (!res) {
    throw new Error("Ошибка запроса добавления пункта");
  }

  return res;
};

export const getPointList = async (
  params?: ApiRequestListOptionsType & { partner_id?: number }
): Promise<ApiResponseType<PointGetData[]>> => {
  const res = await getHttp().get<ApiResponseType<PointGetData[]> | false>(
    "/point/list",
    { searchParams: { ...params } }
  );

  if (!res) {
    throw new Error("Ошибка запроса получения списка пунктов");
  }

  return res;
};

export const getPointDelete = async (
  pointId: number
): Promise<ApiResponseType> => {
  const res = await getHttp().get<ApiResponseType | false>("/point/delete", {
    searchParams: {
      point_id: pointId,
    },
  });

  if (!res) {
    throw new Error("Ошибка запроса удаления пункта");
  }

  return res;
};
