import { getHttp } from "@/kernel/api/http";
import {
  ApiResponseType,
  ApiRequestPartnerListOptionsType,
  ApiResponsePartnerListType,
} from "@/kernel/api/types";
import {
  PartnerGetData,
  PartnerAddData,
  PartnerEditData,
} from "@/kernel/api/dto/Partner";

export const getPartnerGet = async (
  partnerId: number
): Promise<ApiResponseType<PartnerGetData>> => {
  const res = await getHttp().get<ApiResponseType<PartnerGetData> | false>(
    "/partner/get",
    {
      searchParams: {
        partner_id: partnerId,
      },
    }
  );

  if (!res) {
    throw new Error("Ошибка запроса getPartnerGet");
  }

  return res;
};

export const getPartnerDelete = async (
  partner_id: number
): Promise<ApiResponseType> => {
  const res = await getHttp().get<ApiResponseType | false>("/partner/delete", {
    searchParams: {
      partner_id,
    },
  });

  if (!res) {
    throw new Error("Ошибка запроса getPartnerDelete");
  }

  return res;
};

export const getPartnerList = async (
  options?: ApiRequestPartnerListOptionsType
): Promise<ApiResponsePartnerListType> => {
  const res = await getHttp().get<ApiResponsePartnerListType | false>(
    "/partner/list",
    { searchParams: { ...options } }
  );

  if (!res) {
    throw new Error("Ошибка запроса getPartnerList");
  }

  return res;
};

export const postPartnerAdd = async (
  payload: PartnerAddData
): Promise<ApiResponseType> => {
  const res = await getHttp().post<ApiResponseType | false>("/partner/add", {
    ...payload,
    phone: JSON.stringify(payload.phone),
  });

  if (!res) {
    throw new Error("Ошибка запроса postPartnerAdd");
  }

  return res;
};

export const postPartnerEdit = async (
  payload: PartnerEditData
): Promise<ApiResponseType> => {
  const res = await getHttp().post<ApiResponseType | false>("/partner/edit", {
    ...payload,
    phone: JSON.stringify(payload.phone),
  });

  if (!res) {
    throw new Error("Ошибка в запросе редактирования контрагента");
  }

  return res;
};
