import { getHttp } from "@/kernel/api/http";
import { ApiResponseType, ApiSchemeAuth } from "@/kernel/api/types";

export const getAuth = async (
  password: string
): Promise<ApiResponseType<ApiSchemeAuth>> => {
  const res = await getHttp().get<ApiResponseType<ApiSchemeAuth> | false>(
    "/auth",
    {
      searchParams: { password },
    }
  );

  if (!res) {
    throw new Error("Ошибка запроса getAuth");
  }

  return res;
};

export const getAuthDelete = async (): Promise<ApiResponseType> => {
  const res = await getHttp().get<ApiResponseType | false>("/auth/delete");

  if (!res) {
    throw new Error("Ошибка запроса getAuth");
  }

  return res;
};
