export function debounce<P extends any[]>(
  func: (...args: P) => void,
  timeout: number
): (...args: P) => void {
  let timer: number;
  return (...args: P) => {
    if (timer) {
      clearTimeout(timer);
    }

    timer = setTimeout(() => {
      func(...args);
    }, timeout);
  };
}
