import { getHttp } from "@/kernel/api/http";
import { ApiResponseType, ApiRequestListOptionsType } from "@/kernel/api/types";
import { NotRequestException } from "@/kernel/lib/exceptions/not-request.exception";
import { EventGetData } from "@/kernel/api/dto/Event";

export const GetSystem = async (): Promise<ApiResponseType<EventGetData[]>> => {
  const res = await getHttp().get<ApiResponseType<EventGetData[]> | false>(
    "/system/set/get"
  );

  if (!res) {
    throw new NotRequestException(GetSystem.name);
  }

  return res;
};

export const UpdateSystem = async (
  params?: ApiRequestListOptionsType
): Promise<ApiResponseType<EventGetData[]>> => {
  const res = await getHttp().get<ApiResponseType<EventGetData[]> | false>(
    "/system/set/edit",
    { searchParams: { ...params } }
  );

  if (!res) {
    throw new NotRequestException(UpdateSystem.name);
  }

  return res;
};

export const UpdateMembers = async (
  params?: ApiRequestListOptionsType
): Promise<ApiResponseType<EventGetData[]>> => {
  const res = await getHttp().get<ApiResponseType<EventGetData[]> | false>(
    "/chain/member/edit",
    { searchParams: { ...params } }
  );

  if (!res) {
    throw new NotRequestException(UpdateSystem.name);
  }

  return res;
};
