export interface PointEntityDataInterface {
  partner_id: number;
  user_id: number;
  partner_fullname?: string;
  partner_name?: string;
  partner_type?: string;
  name: string;
  address: string;
  region?: string;
  district?: string;
  locality?: string;
  location: string;
  comment?: string;
  subject_to_buy?: string[];
  subject_to_sell?: string[];
  work_from: string;
  work_to: string;
  operation_duration: number;
}

export class PointEntity implements PointEntityDataInterface {
  public point_id!: number;
  public user_id!: number;
  public partner_id: number;
  public partner_fullname?: string;
  public partner_name?: string;
  public partner_type?: string;
  public name: string;
  public address: string;
  public location: string;
  public region?: string;
  public district?: string;
  public locality?: string;
  public comment?: string;
  public subject_to_buy!: string[];
  public subject_to_sell!: string[];
  public work_from: string;
  public work_to: string;
  public operation_duration: number;

  constructor(params: PointEntity) {
    this.point_id = params.point_id;
    this.partner_id = params.partner_id;
    this.user_id = params.user_id;
    this.name = params.name;
    this.address = params.address;
    this.location = params.location;
    this.work_from = params.work_from;
    this.work_to = params.work_to;
    this.operation_duration = params.operation_duration;

    params.locality && (this.locality = params.locality);
    params.region && (this.region = params.region);
    params.district && (this.district = params.district);
    params.comment && (this.comment = params.comment);
    params.partner_type && (this.partner_type = params.partner_type);
    params.partner_fullname &&
      (this.partner_fullname = params.partner_fullname);
    params.partner_name && (this.partner_name = params.partner_name);
    this.subject_to_buy = params.subject_to_buy;
    this.subject_to_sell = params.subject_to_sell;
  }
}
