import { defineStore } from "pinia";
import { Ref, ref } from "vue";
import { SettingEntity } from "@/kernel/entities/Setting";
import {
  GetSystem,
  UpdateSystem,
  UpdateMembers,
} from "@/kernel/api/methods/System";
import { SettingUpdateItem } from "@/kernel/api/dto/Chain";
import { useStore } from "@/store";

export const useStoreSetting = defineStore("settng", () => {
  const settings: Ref<Record<string, SettingEntity[]>> = ref({}); // список агентов.
  const store = useStore();

  // function createValue(data: SettingUpdateItem) {
  //   return new AgentEntity({
  //     ...data,
  //   });
  // }

  async function getSystem(): Promise<any> {
    const res = await GetSystem();

    if (!res.error && res.data) {
      settings.value = JSON.parse(JSON.stringify(res.data));
    } else {
      throw new Error("Ошибка получение настроек");
    }
  }

  async function updateSystem(param: object) {
    const res = await UpdateSystem(param);
    if (!res.error) {
      return GetSystem();
    }
    throw new Error("Ошибка обновление настроек");
  }

  async function updateMembers(param: object) {
    const res = await UpdateMembers(param);
    if (!res.error) {
      return GetSystem();
    }
    throw new Error("Ошибка обновление настроек");
  }

  return {
    updateSystem,
    getSystem,
    updateMembers,
    settings,
  };
});
